.home-about {
  margin-top: 4 * $base-spacing;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  transform-style: preserve-3d;

  &::after {
    display: block;
    content: "";
    position: absolute;
    width: calc(200vw + 100vh - #{4 * $base-spacing});
    height: 100%;
    right: 0;
    z-index: -9;
    background-color: $vanilla;
    transition: transform 3 * $base-duration $base-timing, opacity 3 * $base-duration $base-timing;
    transform: translateX(calc(100vw + 100vh));
    opacity: 0;
    background-image: linear-gradient(135deg, white calc(50% + #{3 * $base-spacing}), $vanilla calc(50% + #{3 * $base-spacing}));

    html.no-js & {
      transform: translateX(0);
      opacity: 1;
    }
  }

  &::before {
    content: "";
    height: 50%;
    position: absolute;
    top: 55%;
    z-index: 99;
    right: 5vw;
    width: 60vw;
    opacity: 0.5;
    background-image: radial-gradient(#fff 20%, transparent 20%);
    background-position: 10px 10px;
    background-size: 7px 7px;

    @media screen and (min-width: $medium-screen) {
      will-change: transform;
      transform-style: preserve-3d;
      transform: translate3d(0, 0, 0.25px) scale(0.75);
    }
  }

  .container {
    transform-style: preserve-3d;
    // position: relative;
    &::after {
      content: "";
      position: absolute;
      display: block;
      height: 100%;
      width: 100%;
      top: 0;
      background-image: url("data:image/svg+xml;base64,");
      background-size: contain;
      background-position: right;
      background-repeat: no-repeat;
    }
  }

  &--text {
    position: relative;
    z-index: 9;
    margin-top: 8 * $base-spacing;
    margin-bottom: 4 * $base-spacing;
    opacity: 0;
    transition: opacity ($base-duration * 3) $base-duration $base-timing, transform ($base-duration * 2) $base-duration $base-timing;
    text-shadow: 0 0 3 * $base-spacing darken($vanilla, 50%);

    html.no-js & {
      opacity: 1;
    }

    @media screen and (min-width: $medium-screen) {
      will-change: transform;
      transform-style: preserve-3d;
      perspective-origin: center center;
      transform: translate3d(0, $small-spacing, 0.2px) scale(0.8);
    }
  }

  &--title {
    font-size: modular-scale(6);
    font-weight: 400;
    color: white;
    width: 65%;
    margin-bottom: $base-spacing;
  }

  &--image {
    position: absolute;
    right: -2 * $base-spacing;
    bottom: -2 * $base-spacing;
    width: 55%;
    height: auto;
    opacity: 0;
    transition: opacity ($base-duration * 3) $base-duration $base-timing, transform ($base-duration * 2) $base-duration $base-timing;
    text-shadow: 0 0 3 * $base-spacing darken($vanilla, 50%);
    transform: translate(0, $small-spacing);

    html.no-js & {
      opacity: 1;
    }

    @media screen and(min-width: $medium-screen) {
      will-change: transform;
      transform-style: preserve-3d;
      transform: translate3d(0, $base-spacing, 0.1px) scale(0.9);
    }
  }

  &.in-view {
    &::after {
      transform: translateX(0);
      opacity: 1;
    }

    .home-about--text {
      opacity: 1;
      transform: translate3d(0, 0, 0.2px) scale(0.8);
    }

    .home-about--image {
      opacity: 1;
      transform: translate3d(0, 0, 0.1px) scale(0.9);
    }
  }
}



